import React, { useState } from 'react';

const CertificationsPopup = ({ onClose }) => {
  const [active, setActive] = useState(null);

  const titleClass = (key) =>
    `text-[10vh] font-semibold leading-none cursor-pointer transition duration-200 ${
      active === null
        ? 'opacity-100'
        : active === key
        ? 'opacity-100'
        : 'opacity-0 pointer-events-none'
    }`;

  return (
    <div className="relative w-full h-full overflow-visible font-robotoCondensed">
      <button
        onClick={onClose}
        className="absolute right-4 top-4 z-50 text-xl opacity-70 hover:opacity-100 p-2 transition-opacity"
      >
        
      </button>

      {/* LEFT SIDE TITLES */}
      <div className="absolute left-[20vh] top-1/2 -translate-y-1/2 flex flex-col gap-6 items-start z-30">
        <h3
          onMouseEnter={() => setActive('afp')}
          onMouseLeave={() => setActive(null)}
          className={titleClass('afp')}
        >On the Job Training
        </h3>

        <h3
          onMouseEnter={() => setActive('sap')}
          onMouseLeave={() => setActive(null)}
          className={titleClass('sap')}
        >SAP Business One
        </h3>

        <h3
          onMouseEnter={() => setActive('oracle')}
          onMouseLeave={() => setActive(null)}
          className={titleClass('oracle')}
        >Database Foundations
        </h3>
      </div>

      {/* AFP SECTION */}
      <div id="afp" className={`absolute inset-0 transition-opacity duration-300 ${
          active === 'afp' ? 'opacity-100' : 'opacity-0 pointer-events-none'
        }`}
      >
        <div className="relative w-full h-full flex items-center justify-between p-10">
          <div className="absolute right-0 top-0 w-1/2 h-full overflow-visible pointer-events-none">
            <img
              src="https://upload.wikimedia.org/wikipedia/commons/thumb/1/10/Seal_of_the_Armed_Forces_of_the_Philippines.svg/1659px-Seal_of_the_Armed_Forces_of_the_Philippines.svg.png"
              alt=""
              className="w-full h-full object-contain opacity-15 scale-125 -translate-y-30 -translate-x-40"
            />
          </div>

          <div className="absolute top-[5vh] left-[10vh] z-20 text-start">
            <h3 className="text-[5vh] font-semibold">Armed Forces of the Philippines</h3>
          </div>

          <div className="absolute bottom-[15vh] left-[15vh] z-20 text-start max-w-[80vh]">
            <ul className="list-disc pl-5 space-y-2 text-[4vh]">
              <li>Completed 486 hours of on-the-job training as a Software Developer Intern.</li>
              <li>Gained hands-on experience in web development and software testing.</li>
            </ul>
          </div>

          <h3 className="absolute bottom-10 right-10 z-20 text-[30vh] font-semibold leading-none">
            2024
          </h3>
        </div>
      </div>

      {/* SAP SECTION */}
      <div id="sap" className={`absolute inset-0 transition-opacity duration-300 ${
          active === 'sap' ? 'opacity-100' : 'opacity-0 pointer-events-none'
        }`}
      >
        <div className="relative w-full h-full flex items-center justify-between p-10">
          <div className="absolute w-full h-full overflow-visible pointer-events-none">
            <img
              src="https://www.aimbetter.com/wp-content/uploads/2019/02/SAP-B1-Logo1.png"
              alt=""
              className="w-full h-full object-contain opacity-15 scale-125"
            />
          </div>

          <div className="absolute top-[5vh] left-[10vh] z-20 text-start">
            <h3 className="text-[5vh] font-semibold">STI College Cubao</h3>
          </div>

          <div className="absolute bottom-10 left-1/2 -translate-x-1/2 z-20 max-w-[110vh] w-full text-center">
            <ul className="list-disc space-y-2 text-[4vh] text-left">
              <li>Trained in SAP Business One fundamentals, including system navigation, basic configuration, and business process integration.</li>              
            </ul>
          </div>

          <h3 className="absolute right-10 z-20 text-[30vh] font-semibold leading-none">2023</h3>
        </div>
      </div>

      {/* ORACLE SECTION */}
      <div id="oracle" className={`absolute inset-0 transition-opacity duration-300 ${
          active === 'oracle' ? 'opacity-100' : 'opacity-0 pointer-events-none'
        }`}
      >
        <div className="relative w-full h-full flex items-center justify-between p-10">
          <div className="absolute w-full h-full overflow-visible pointer-events-none">
            <img
              src="https://thetechblock.com/wp-content/uploads/2021/02/oracle-database-logo.png"
              alt=""
              className="w-full h-full object-contain opacity-15 scale-125"
            />
          </div>

          <div className="absolute top-[5vh] left-[10vh] z-20 text-start">
            <h3 className="text-[5vh] font-semibold">STI College Cubao</h3>
          </div>

          <div className="absolute bottom-10 left-1/2 -translate-x-1/2 z-20 max-w-[110vh] w-full text-center">
            <ul className="list-disc space-y-2 text-[4vh] text-left">
              <li>Completed Oracle Database Foundations training, covering core database concepts such as SQL fundamentals, data modeling, table relationships, and database administration.</li>
            </ul>
          </div>

          <h3 className="absolute top-10 right-10 z-20 text-[30vh] font-semibold leading-none">2022</h3>
        </div>
      </div>
    </div>
  );
};

export default CertificationsPopup;
