import React, { useState, useEffect, useRef } from 'react';
import gsap from 'gsap';

import ohpi from '../../assets/images/ohpi.png';
import message from '../../assets/images/messaging.png';
import inventory from '../../assets/images/ictinvent.png';
import meee from '../../assets/images/Program_Camille.jpg';
import ohpiVideo from '../../assets/videos/orient.mp4';
import messageVideo from '../../assets/videos/Messaging.webm';
import inventoryVideo from '../../assets/videos/inventory.mp4';
import employeeVideo from '../../assets/videos/Employee.mp4';

const projects = [
  {
    title: 'Orient Holdings',
    image: ohpi,
    description: 'A company profile website showcasing services and portfolio. Developed and deployed a responsive company website featuring a modern, clean design with engaging user experiences across various devices using Bootstrap, HTML, CSS, and JavaScript',
  },
  {
    title: 'Messaging',
    image: message,
    description: 'Designed and developed a secure messaging application using the .NET Framework with C#, featuring end-to-end encryption, message transmission, user authentication, and real-time updates.',
  },
  {
    title: 'Inventory',
    image: inventory,
    description: 'Inventory management system with tracking features. Developed a web-based inventory management system using PHP (Laravel) and Vue.js, implementing full CRUD functionality to efficiently track, manage, and update stock levels.',
  },
  {
    title: 'Employee',
    image: meee,
    description: 'Employee portal to manage HR and internal workflows.',
  },
];

const projectVideos = {
  'Orient Holdings': ohpiVideo,
  'Messaging': messageVideo,
  'Inventory': inventoryVideo,
  'Employee': employeeVideo,
};

const ProjectsPopup = ({ onClose }) => {
  const [activeProject, setActiveProject] = useState(null);
  const [clickedProject, setClickedProject] = useState(null);
  const [hoveredIndex, setHoveredIndex] = useState(null);
  const [isAnimating, setIsAnimating] = useState(true);
  const [isVideo, setIsVideo] = useState(false);

  const containerRef = useRef(null);
  const closeBtnRef = useRef(null);
  const titleRefs = useRef([]);
  const imageRef = useRef(null);
  const videoRef = useRef(null);

  const displayedProject = clickedProject || activeProject;

  // 1. Initial entrance animation
  useEffect(() => {
    const validTitles = titleRefs.current.filter(Boolean);
    let ctx = gsap.context(() => {
      gsap.set(validTitles, { x: 100, autoAlpha: 0 });
      gsap.set(closeBtnRef.current, { autoAlpha: 0 });

      const tl = gsap.timeline();
      tl.to(validTitles, {
        x: 0,
        autoAlpha: 1,
        duration: 1,
        stagger: 0.15,
        ease: 'power3.out',
        delay: 0.2,
        onComplete: () => setIsAnimating(false),
      }).to(closeBtnRef.current, { autoAlpha: 1, duration: 0.5 }, "-=0.5");
    }, containerRef);
    return () => ctx.revert();
  }, []);

  // 2. Handle Hover/Click Logic for Titles (SHRINK EFFECT ADDED HERE)
  useEffect(() => {
    if (isAnimating) return;
    const validTitles = titleRefs.current.filter(Boolean);

    let ctx = gsap.context(() => {
      validTitles.forEach((el, i) => {
        const isClicked = clickedProject && projects[i].title === clickedProject.title;
        const isHovered = hoveredIndex === i;

        if (clickedProject) {
          if (isClicked) {
            const vh = window.innerHeight;
            const rect = el.getBoundingClientRect();
            gsap.to(el, {
              y: (vh * 0.7) - rect.top,
              x: 0,
              scale: 1.5,
              autoAlpha: 1,
              duration: 0.8,
              ease: 'power3.out',
            });
          } else {
            gsap.to(el, { autoAlpha: 0, scale: 0.8, x: 50, duration: 0.4 });
          }
        } else {
          // HOVER LOGIC
          let targetScale = 1;
          let targetAlpha = 1;

          if (hoveredIndex !== null) {
            if (isHovered) {
              targetScale = 1.25; // Grow the hovered one
              targetAlpha = 1;
            } else {
              targetScale = 0.9; // Shrink the others
              targetAlpha = 0.3; // Fade the others
            }
          }

          gsap.to(el, {
            y: 0, 
            x: 0, 
            scale: targetScale,
            autoAlpha: targetAlpha,
            duration: 0.4,
            ease: 'power2.out',
            overwrite: true
          });
        }
      });
    }, containerRef);
    return () => ctx.revert();
  }, [clickedProject, hoveredIndex, isAnimating]);

  // 3. Image/video scaling logic
  useEffect(() => {
    if (!displayedProject || !imageRef.current) return;

    const updateAnimation = () => {
      if (!imageRef.current) return;
      const vh = window.innerHeight;
      const vw = window.innerWidth;
      const baseHeight = imageRef.current.offsetHeight;
      const targetScale = (vh * 0.65) / baseHeight;
      const targetX = vw * 0.08;
      const targetY = -vh * 0.07;

      if (clickedProject) {
        gsap.to(imageRef.current, {
          scale: targetScale,
          x: targetX,
          y: targetY,
          duration: 0.6,
          ease: 'power3.out',
          onComplete: () => setIsVideo(true),
        });
      } else {
        gsap.to(imageRef.current, { scale: 1, x: 0, y: 0, opacity: 1, duration: 0.4 });
      }
    };

    updateAnimation();
    window.addEventListener('resize', updateAnimation);
    return () => window.removeEventListener('resize', updateAnimation);
  }, [clickedProject, displayedProject]);

  // 4. Video Sync
  useEffect(() => {
    if (isVideo && videoRef.current && imageRef.current) {
      const vh = window.innerHeight;
      const vw = window.innerWidth;
      const baseHeight = imageRef.current.offsetHeight;
      const targetScale = (vh * 0.65) / baseHeight;

      gsap.set(videoRef.current, {
        scale: targetScale,
        x: vw * 0.08,
        y: -vh * 0.07,
        opacity: 0,
      });
      gsap.to(videoRef.current, { opacity: 1, duration: 0.4 });
    }
  }, [isVideo]);

  const handleBackOrClose = () => {
    if (clickedProject) {
      setClickedProject(null);
      setHoveredIndex(null);
      setIsVideo(false);
      return;
    }
    const validTitles = titleRefs.current.filter(Boolean);
    const tl = gsap.timeline({ onComplete: onClose });
    tl.to(closeBtnRef.current, { autoAlpha: 0, duration: 0.3 });
    tl.to(validTitles, { x: 100, autoAlpha: 0, duration: 0.4, stagger: 0.1 });
  };

  return (
    <div ref={containerRef} className="relative w-full h-full flex font-robotoCondensed">
      <button ref={closeBtnRef} onClick={handleBackOrClose} className="absolute right-4 top-4 z-50 text-xl p-2 cursor-pointer">
        {clickedProject ? <span className="inline-block [transform:scale(1.5)]"></span> : ''}
      </button>

      {/* Left Side (Media) */}
      <div className="w-1/2 flex flex-col items-center justify-center pointer-events-none relative">
        {displayedProject && (
          <>
            <img
              ref={imageRef}
              src={displayedProject.image}
              style={{ opacity: isVideo ? 0 : 1 }}
              className="max-w-[80%] max-h-[70%] object-contain rounded-md absolute"
            />
            {isVideo && (
              <video
                ref={videoRef}
                src={projectVideos[displayedProject.title]}
                className="max-w-[80%] max-h-[70%] object-contain rounded-md absolute"
                autoPlay muted loop
              />
            )}
          </>
        )}
      </div>

      {/* Description */}
      {clickedProject && (
        <p className="absolute right-0 top-1/2 -translate-y-[100%] -translate-x-5 mt-4 text-left text-[2.5vh] max-w-[57vh] animate-fade-scale">
          {clickedProject.description}
        </p>
      )}

      {/* Right Side (List) */}
      <div className="w-1/2 h-full">
        <section className={`h-full flex flex-col items-end justify-center px-16 ${isAnimating ? 'pointer-events-none' : ''}`}>
          {projects.map((project, index) => (
            <h3
              key={project.title}
              ref={(el) => (titleRefs.current[index] = el)}
              className="project-title whitespace-nowrap cursor-pointer opacity-0 invisible origin-right"
              onMouseEnter={() => !clickedProject && (setHoveredIndex(index), setActiveProject(project))}
              onMouseLeave={() => !clickedProject && (setHoveredIndex(null), setActiveProject(null))}
              onClick={() => setClickedProject(project)}
            >
              {project.title}
            </h3>
          ))}
        </section>
      </div>
    </div>
  );
};

export default ProjectsPopup;